}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.fullItem {
  margin: 0 auto;
  padding-left: 5px;

}

.itemTitle {
  font-size: 1.2em;
}

p {
  font-size: 12px;
  margin:0
}

p,b {
  font-family: Georgia, Times, "Times New Roman", serif;
}


input {

  margin-left: 10px;
}
label {
  color: white;
  margin-left: 10px;
  font-size: 12px;
}

ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  list-style-type: none;
  padding-left: 0;
  width:100%;
}

ul li {
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-preferred-size: calc(25% - 4px);
      flex-basis: calc(25% - 4px);
